.spec_grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: 360px;
    grid-gap: 30px;
}
@media (max-width: 1024px) {
    .spec_grid {
        grid-template-columns: repeat(2, 1fr);
    }
}
@media (max-width: 768px) {
    .spec_grid {
        grid-template-columns: 1fr;
    }
}