.home {
    display: flex;
    align-items: center;
    grid-column-gap: 30px;
    text-decoration: none;
}
.name {
    font-family: 'Merriweather', serif;
    font-weight: 900;
    font-size: 36px;
    line-height: 50px;
    color: #005FA3;
}
@media (max-width: 560px) {
    .home {
        grid-column-gap: 11px;
    }
    .home img {
        width: 25px;
        height: 25px;
    }
    .name {
        font-size: 22px;
        line-height: 138.27%;
    }
}